.wrapper {
  position: relative;
}
.wrapper::before,
.wrapper::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper::before {
  display: none;
  top: 0;
}
.wrapper::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper::before,
  .wrapper::after {
    transform: scaleY(0.33);
  }
}
.wrapper > img {
  display: block;
  width: 100%;
  height: 4.5rem;
  max-height: 500px;
}
.wrapper_content {
  background-color: #FFF;
  padding: 8px 8px 0;
}
.wrapper_content_title {
  margin-bottom: 4px;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper_content_des {
  color: #AEAEAE;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
@media screen and (max-width: 321px) {
  .wrapper_content_des {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_content_des {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_content_des {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_content_footer {
  display: flex;
  color: #AEAEAE;
  padding: 8px 0;
  margin-top: 8px;
  position: relative;
}
@media screen and (max-width: 321px) {
  .wrapper_content_footer {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_content_footer {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_content_footer {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_content_footer::before,
.wrapper_content_footer::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_content_footer::before {
  top: 0;
}
.wrapper_content_footer::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_content_footer::before,
  .wrapper_content_footer::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_content_footer::before,
  .wrapper_content_footer::after {
    transform: scaleY(0.33);
  }
}
.wrapper_content_footer > li {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper_content_footer > li > i {
  margin-right: 4px;
}
.wrapper_content_footer > li:nth-child(2) {
  position: relative;
}
.wrapper_content_footer > li:nth-child(2)::before,
.wrapper_content_footer > li:nth-child(2)::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_content_footer > li:nth-child(2)::before {
  left: 0;
}
.wrapper_content_footer > li:nth-child(2)::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_content_footer > li:nth-child(2)::before,
  .wrapper_content_footer > li:nth-child(2)::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_content_footer > li:nth-child(2)::before,
  .wrapper_content_footer > li:nth-child(2)::after {
    transform: scaleX(0.33);
  }
}
