.wrapper {
  height: 100%;
  overflow-y: auto;
}
.wrapper_info {
  position: relative;
  padding: 50px 15px 0;
}
.wrapper_info_avator {
  position: absolute;
  top: 0;
  left: 50%;
  width: 80px;
  height: 80px;
  margin: -40px 0 0 -40px;
  padding: 1px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.wrapper_info_avator img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wrapper_info_detail {
  text-align: center;
  padding-bottom: 8px;
  position: relative;
}
.wrapper_info_detail::before,
.wrapper_info_detail::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px dashed #DDD;
  left: 0;
  right: 0;
}
.wrapper_info_detail::before {
  display: none;
  top: 0;
}
.wrapper_info_detail::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_info_detail::before,
  .wrapper_info_detail::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_info_detail::before,
  .wrapper_info_detail::after {
    transform: scaleY(0.33);
  }
}
@media screen and (max-width: 321px) {
  .wrapper_info_detail_name {
    /*小*/
    font-size: 16px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info_detail_name {
    /*中*/
    font-size: 17px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info_detail_name {
    /*大*/
    font-size: 19px;
  }
}
.wrapper_info_detail_address {
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .wrapper_info_detail_address {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info_detail_address {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info_detail_address {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_filter {
  color: #FFF;
  margin: 10px 0;
}
@media screen and (max-width: 321px) {
  .wrapper_filter {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_filter {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_filter {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_filter > li {
  display: inline-block;
  padding: 4px 6px;
  margin-right: 4px;
  background-color: #ECECEC;
}
.wrapper_filter .active {
  color: #AEAEAE;
}
